.footer {
  background-color: black;
  padding: 20px;
  text-align: center;
  border-top: 2px solid #470287;
}

.footer-content {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.footer p {
  margin: 0;
}

.footer-links {
  list-style-type: none;
  padding: 0;
  display: flex;
  justify-content: center;
  margin-top: 10px;
}

.footer-links li {
  margin: 0 10px;
}

.footer-links a {
  color: #555555;
  text-decoration: none;
}

.footer-links a:hover {
  text-decoration: underline;
}

.madeBy {
  font-size: smaller;
  display: inline;
}

.linkAmaroke {
  color: #470287;
}